@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
  font-family: "Poppins";
}
body {
  display: flex;
  text-align: center;
}

//Header
header {
  width: 100%;
  margin-bottom: 25px;
  margin-top: 15px;
  & #photoApp {
    color: #75099b;
    font-weight: 400;
  }
  & #myPhotos {
    color: #0e71cc;
    font-weight: 400;
  }
  & #goBackFav {
    color: #a39898;
    text-decoration: none;
    font-size: small;
  }
}

//PAGES
#grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

//SEARCH

#searchFilter {
  height: 30px;
  background-color: #e7e7e7;
  width: 240px;
  text-indent: 2%;
  border-radius: 25px;
  margin-bottom: 25px;
}
#searchIcon {
  color: #bdb5b5;
  padding-top: 5px;
  background-color: transparent;
  border: none;
}

//BOTON ORDER
#order {
  color: #a39898;
  margin-bottom: 15px;
  text-decoration: none;
  font-size: small;
  border: none;
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  appearance: none;
  padding-right: 25px;
  background-image: url(./assets/down-arrow.png);
  background-repeat: no-repeat;
  background-position: right;
  background-size: 15px;
  & option {
    font-size: 10px;
  }
}

//Favorite and search photo
.favoritePhoto {
  padding-bottom: 40px;
  margin: 0 50px;
  & h2 {
    padding-bottom: 10px;
  }
}
.imgAndButtons {
  position: relative;
  display: flex;
  width: 200px;
  margin-bottom: 180px;
  & img {
    object-fit: cover;
    position: absolute;
    border-radius: 30px;
    left: -15px;
    width: 220px;
    height: 200px;
  }
  & div {
    position: relative;
    width: 110px;
    top: 165px;
    left: 50px;
  }
  & .divButtonsHome {
    left: 65px;
  }
}

#buttons {
  color: #ffffff;
  background-color: transparent;
  border: none;
  padding: 0 2px;
}

#data {
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 250px;
}

//modal of the image selected
#overlay {
  background-color: #ffffff;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9;
}
#modal {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 80%;
  height: 70%;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 8;
}

#cancelModal {
  width: 70px;
  height: 70px;
}
#imageModal {
  object-fit: cover;
  width: 100%;
  max-height: 1050px;

  border-radius: 30px;
}
#textarea {
  border-style: none;
  border-color: Transparent;
  overflow: hidden;
  width: 60%;
  height: 150px;
  resize: none;
  font-size: 25px;
  font-weight: 600;
}
#saveButton {
  color: #000000;
  background-color: transparent;
  border: none;
  transform: scale(2.8);
  padding: 0 2px;
}
#modalData {
  display: flex;
  justify-content: space-between;
}

@media only screen and (min-width: 709px) {
  //HEADER
  header {
    & h1 {
      font-size: 80px;
    }
    & #goBackFav {
      font-size: 30px;
    }
  }
  //SEARCH

  #searchFilter {
    display: flex;
    align-items: center;
    height: 50px;
    width: 440px;
    padding-left: 30px;
    margin-bottom: 30px;
  }
  #searchIcon {
    padding-left: 160px;
  }

  //BOTON ORDER
  #order {
    font-size: larger;
    padding-right: 40px;
    background-size: 25px;
    margin-bottom: 30px;
  }
  //PAGES
  #data {
    & h2 {
      font-size: 30px;
    }
    & h4 {
      font-size: 20px;
    }
  }
}
